@font-face{font-family:'Lato';src:url(/fonts/Lato-Regular.ttf) format("truetype");src:url(/fonts/Lato-Regular.woff) format("woff");src:url(/fonts/Lato-Regular.woff2) format("woff2");font-display:swap}*{color:#ffffff}* body{width:100%;height:100vh;margin:0;padding:0;font-family:'Lato';background:#111111;overflow:hidden;text-shadow:0.85px 1.55px 2px #111111}* body .header{position:relative;z-index:2;width:100%;background:#333333;margin:0;display:inline-block;min-height:5vh;padding:0;box-shadow:0.25px 1.2px 1.9px 0.33px #111111}* body .header nav{margin:1.2% auto}* body .header nav #nav-list{padding:0 0.15vw;list-style:none;height:100%}* body .header nav #nav-list .nav-link{display:inline-block;width:23.5%;text-align:center}* body .header nav #nav-list .nav-link a span{font-size:2.95vw}* body .header nav #nav-list .nav-dots{display:inline-block;width:6%;text-align:center;position:relative;bottom:1.5px}* body .header nav #nav-list .nav-dots a span{font-size:2.75vw}@media (max-device-width: 700px) and (orientation: portrait){body .header nav #nav-list .nav-link a span{font-size:4vw}body .header nav #nav-list .nav-dots a span{font-size:3.75vw}}@media (max-device-width: 500px){body .header{min-height:4vh}body .header nav #nav-list{margin:3.5% auto}body .header nav #nav-list .nav-link{width:23.5%}body .header nav #nav-list .nav-link a span{font-size:6vw}body .header nav #nav-list .nav-dots{width:6%}body .header nav #nav-list .nav-dots a span{font-size:5.25vw}}@media (max-device-width: 379px){body .header nav #nav-list .nav-link a span{font-size:7vw}body .header nav #nav-list .nav-dots a span{font-size:6.5vw}}@media (orientation: landscape){body .header nav #nav-list{margin:0}}
