body #clock-container{overflow:hidden;background:#010125;height:100vh}body #clock-container #world-cont{position:absolute;width:100%;bottom:2.5%;display:block;background:inherit}body #clock-container #world-cont #world-button{position:relative;display:block;font-size:6.5vw;border-radius:25%;margin:0 auto;outline:none;background:transparent;opacity:0.8;text-shadow:0.9px 0.99px 2.3px silver}body #clock-container #world-cont #world-button:active{bottom:-7.5px}body #clock-container #world-cont #world-button:hover{border:1px solid #ffffff}body #clock-container #heading-container{height:80vh;overflow-y:hidden;display:block}body #clock-container #heading-container h1,body #clock-container #heading-container h3{text-align:center;text-shadow:0.8px 1.8px 2.3px #000000;opacity:0.8;font-weight:100}body #clock-container #heading-container #heading-time{font-size:60px;margin:1.5% auto 1.75%}body #clock-container #heading-container h3{font-size:24px;margin:1.25% auto}body #clock-container #heading-container #alarm_pres{font-size:18px;margin:0 auto;opacity:0.6}body #clock-container #heading-container #alarm_pres span{margin-left:1.15%}body #clock-container #heading-container hr{box-shadow:1.25px 1.95px 2.75px #000000;opacity:0.4}body #clock-container #heading-container #selected-list{box-sizing:border-box;list-style:none;opacity:0.87;overflow-y:auto;height:80%;display:block;font-size:16px;padding:0 0 8% 0;margin:24px auto;width:75%;text-align:center;scroll-behavior:smooth}body #clock-container #heading-container #selected-list li{padding:10px;font-weight:100}body #clock-container #clock-list{list-style:none;display:block;text-align:left;font-size:16px;width:100vw;height:75vh;overflow:auto;margin:15px 0 0 0;padding-left:20px;scroll-behavior:smooth}body #clock-container #clock-list li{padding:0.5vw;font-weight:100}body #clock-container #clock-list li .check{display:inline-block;margin:1.75vw;position:relative;top:0.4vh;width:16.5px;height:16.5px}@media screen and (max-device-width: 4